============================\nLight/Dark Mode Switch Toggle Styling\n====================================================== */\n\n.toggle-wrapper {\n  display: flex;\n  margin-bottom: 0.5em;\n  column-gap: 30em;\n  justify-content: flex-end;\n}\n\n.switch {\n  position: relative;\n  float: right;\n  width: 55px;\n  height: 25px;\n  margin: 1em 3em;\n}\n\n/* Hide default HTML checkbox */\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n/* The slider */\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 19px;\n  width: 19px;\n  left: 3.5px;\n  bottom: 3.5px;\n  background-color: white;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\ninput:checked + .slider {\n  background-color: maroon;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px maroon;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  -ms-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n/* Rounded sliders */\n.slider.round {\n  border-radius: 25px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n/* ======================================================\nProduct Detail Styling\n====================================================== */\n\n.pd-flex-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-content: center;\n  margin: 0em;\n}\n\n.nav-bar {\n  display: flex;\n  justify-content: space-between;\n  padding: 0rem 1rem;\n  /* background: linear-gradient(90deg, rgba(219,19,19,1) 0%, rgba(245,203,9,1) 33%, rgba(245,203,9,1) 66%, rgba(219,19,19,1) 100%); */\n  color: white;\n  margin-bottom: 0.3rem;\n}\n\n#search-products{\n  display: inline-block;\n  align-self: center;\n  padding: 1em 2em 0em 2em;\n}\n\n#search-input-form {\n  background: none;\n  color: white;\n  border: none;\n  border-bottom: 0.1rem solid white;\n  width: 12em;\n}\n\n#search-input-form::placeholder {\n  color: white;\n  opacity: 1;\n}\n\n.site-wide-announcement {\n  text-align: center;\n  /* display: inline-block; */\n  align-self: center;\n  margin: 0.75rem 0rem;\n}\n\n/* ======================================================\nProduct Information (Primarily Image Gallery) Styling\n====================================================== */\n\nbutton, select {\n  cursor: pointer;\n}\n\n.star-ratings-reviews {\n  display: flex;\n  align-items: baseline;\n}\n\n.read-all-reviews {\n  margin-left: 0.5rem;\n  text-decoration: underline;\n  font-size: small;\n  cursor: pointer;\n\n}\n\n.product-category p{\n  text-transform: uppercase;\n  margin-bottom: 0em;\n}\n\n.product-name p{\n  font-size: xx-large;\n  font-weight: bold;\n  margin-top: 0em;\n  margin-bottom: 0em;\n  text-decoration: underline dotted;\n}\n\n.product-price p {\n  margin-bottom: 0;\n  font-size: large;\n}\n\n.sale-price {\n  font-weight: bold;\n  color: red;\n}\n\n.product-overview-container {\n  align-self: center;\n  display: flex;\n  justify-content: space-evenly;\n  margin: 1rem 1rem 2rem 1rem;\n}\n\n.product-slogan-description {\n  padding-right: 2em;\n}\n\n.product-features {\n  border-left: inset;\n  align-self: center;\n  flex-basis: 40%;\n}\n\n#gallery-and-styles-container {\n  display: flex;\n  /* flex-wrap: wrap; */\n  justify-content: center;\n  margin: 2px 150px 10px 100px;\n  padding: 10px 20px;\n}\n\n#product-info-container {\n  flex-direction: column;\n}\n\n#image-gallery-container {\n  margin: 0px 2.5em;\n  display: flex;\n  justify-content: center;\n  transition: all 1s ease;\n  align-items: center;\n}\n\n#carousel-thumbnail-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin-right: 0.5rem;\n}\n\n.carousel-item-underlay {\n  width: 50px;\n  height: 50px;\n  padding: 0.5rem;\n  z-index: -5;\n  border-bottom-style: dashed;\n  position: absolute;\n}\n\n\n.carousel-